const useAccordion = () => {
  const accordion = document.querySelector('.accordion');
  /* eslint-disable-next-line */
  const accordionItem = document.querySelectorAll('.accordion-item');

  const open = (button, dropdown) => {
    /* eslint-disable-next-line */
    dropdown.style.height = "".concat(dropdown.scrollHeight, "px");
    button.classList.add('active');
    dropdown.classList.add('active');
  };

  const close = (button, dropdown) => {
    button.classList.remove('active');
    /* eslint-disable-next-line */
    dropdown.classList.remove('active');
    /* eslint-disable-next-line */
    dropdown.style.height = '';
  };

  accordion.addEventListener('click', (e) => {
    /* eslint-disable-next-line */
    const target = e.target;

    if (target.classList.contains('accordion-title')) {
      const parent = target.closest('.accordion-item');
      /* eslint-disable-next-line */
      const dropdown = parent.querySelector('.accordion-description');
      /* eslint-disable-next-line */
      dropdown.classList.contains('active') ? close(target, dropdown) : open(target, dropdown);
    }
  });
};

useAccordion();

const headerBlack = () => {
  const trigger = document.getElementById('about-casino');
  const bw = document.body.clientWidth;
  const headerAnimationTrigger = 'body';
  const headerAnimationStart = `${window.innerHeight}px top`;

  if (bw > 480) {
    ScrollTrigger.create({
      /* eslint-disable-next-line */
      trigger: trigger,
      start: 'top 120px',
      end: 'top 120px',
      onEnter: function onEnter() {
        $('.header-logo__text').addClass('inactive');
        $('.header-logo__img').removeClass('d-none');
        $('.header').addClass('active');
        $('.header-buttons-top').addClass('d-none');
        $('.header-buttons-icons').addClass('active');
      },
      onEnterBack: function onEnterBack() {
        $('.header-logo__text').removeClass('inactive');
        $('.header-logo__img').addClass('d-none');
        $('.header').removeClass('active');
        $('.header-buttons-top').removeClass('d-none');
        $('.header-buttons-icons').removeClass('active');
      },
    });

    ScrollTrigger.create({
      trigger: headerAnimationTrigger,
      start: headerAnimationStart,
      end: '5000% top',
      onUpdate: (self) => {
        if (self.direction === 1 && !$('body').hasClass('no-skroll')) {
          if (bw > 480) {
            $('.header').addClass('scroll');
          }
        } else {
          $('.header').removeClass('scroll');
        }
      },
    });
  }
};

headerBlack();

const toggleMenu = () => {
  const burger = document.querySelector('.burger');
  const popupMenu = document.querySelector('.popup-menu');
  const popupMenuContent = document.querySelector('.popup-menu_content');

  const handlerMenu = () => {
    popupMenuContent.classList.toggle('active-menu');
  };

  burger.addEventListener('click', () => {
    popupMenu.style.display = 'block';
    setTimeout(() => {
      handlerMenu();
    });
  });
  popupMenu.addEventListener('click', (e) => {
    if (e.target.matches('.popup-menu') || e.target.closest('.nav-link') || e.target.closest('.close-menu')) {
      popupMenu.style.display = 'none';
      handlerMenu();
    }
  });
};

toggleMenu();

const scrollTo = () => {
  document.body.addEventListener('click', (e) => {
    if (e.target.closest('a[href^="#"]')) {
      e.preventDefault();
      const target = e.target.closest('a[href^="#"]').getAttribute('href');
      document.querySelector(target).scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      });
    }
  });
};

scrollTo();

const tabs = () => {
  const tabHeader = document.querySelector('.casino-tab-header');
  const tab = document.querySelectorAll('.casino-header-tab');
  const tabContent = document.querySelectorAll('.casino-tab');

  const toggleTabContent = (index) => {
    for (let i = 0; i < tabContent.length; i++) {
      if (index === i) {
        tab[i].classList.add('active');
        tabContent[i].classList.remove('d-none');
      } else {
        tab[i].classList.remove('active');
        tabContent[i].classList.add('d-none');
      }
    }
  };

  tabHeader.addEventListener('click', (e) => {
    /* eslint-disable-next-line */
    const target = e.target;

    if (target.classList.contains('casino-header-tab')) {
      tab.forEach((item, i) => {
        if (item === target) {
          toggleTabContent(i);
        }
      });
    }
  });
};

tabs();

const openGamesPopup = () => {
  const gamesBtn = document.querySelector('.casino-tab-games-buttons');
  const btn = document.querySelectorAll('.btn-game');
  const btnGamePopup = document.querySelectorAll('.btn-game-popup ');

  const toggleBtnPopup = (index) => {
    for (let i = 0; i < btnGamePopup.length; i++) {
      if (index === i) {
        btn[i].classList.add('active');
        btnGamePopup[i].classList.add('active');
      } else {
        btn[i].classList.remove('active');
        btnGamePopup[i].classList.remove('active');
      }
    }
  };

  gamesBtn.addEventListener('click', (e) => {
    btn.forEach((item, i) => {
      if (item === e.target) {
        toggleBtnPopup(i);
      }

      if (e.target.closest('.btn-game-popup__close')) {
        btn[i].classList.remove('active');
        for (let j = 0; j < btnGamePopup.length; j++) {
          btnGamePopup[j].classList.remove('active');
        }
      }
    });
  });
};

openGamesPopup();

