function maskPhone(selector, masked = '+_______________') {
  const elems = document.querySelectorAll(selector);

  function mask(event) {
    /* eslint-disable-next-line */
    const keyCode = event.keyCode;
    const template = masked;
    /* eslint-disable-next-line */
    const	def = template.replace(/\D/g, '');
    /* eslint-disable-next-line */
    const	val = this.value.replace(/\D/g, '');
    let i = 0;
    /* eslint-disable-next-line */
    let newValue = template.replace(/[_\d]/g, a => (i < val.length ? val.charAt(i++) || def.charAt(i) : a));
    i = newValue.indexOf('_');
    if (i !== -1) {
      newValue = newValue.slice(0, i);
    }
    /* eslint-disable-next-line */
    let reg = template.substr(0, this.value.length).replace(/_+/g, a => '\\d{1,' + a.length + '}').replace(/[+()]/g, '\\$&');
    /* eslint-disable-next-line */
    reg = new RegExp('^' + reg + '$');
    /* eslint-disable-next-line */
    if (!reg.test(this.value) || this.value.length < 5 || keyCode > 47 && keyCode < 58) {
      this.value = newValue;
    }
    if (event.type === 'blur' && this.value.length < 5) {
      this.value = '';
    }
  }
  /* eslint-disable-next-line */
  for (const elem of elems) {
    elem.addEventListener('input', mask);
    elem.addEventListener('focus', mask);
    elem.addEventListener('blur', mask);
  }
}

